import React from "react";
import { Image } from "react-bootstrap";
import ContactPhoto from "../media/images/contact.jpg";
import "../styles/contactImage.css";

export default function ContactImage()
{
	return (<div className={'contact-container'}>
		<Image id={'contact-image'} className={'contact-image'} src={ContactPhoto}/>
		<div className={'image-centered-text'}>Contáctenos</div>
	</div>);
}