import React from "react";
import slide1 from '../media/images/gato3.jpg';
import '../styles/information-container1.css';
import Image from "react-bootstrap/Image";

export default function InformationContainer1()
{
	return (<div className={'container'}>
		<div className={'container-image container-panel'}>
			{/*<img src={slide1} alt={''} width={800} height={450}/>*/}
			<Image src={slide1} fluid/>
		</div>
		<div className={'container-content container-panel'}>
			<div className={'content'}>
				<div className={'content-child'}>
					<div className={'content-title'}>Titulo</div>
					<div className={'content-text'}>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit. Et, necessitatibus, officiis. Ab
						doloribus esse eveniet itaque nesciunt odit quod rerum veniam voluptas voluptate. Blanditiis,
						libero perferendis recusandae sed voluptas voluptates.
					</div>
				</div>
			</div>
		</div>
	</div>);
}