import React, { Fragment, useRef} from "react";
import { Form, Image } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Call from "../media/images/call.svg";
import Mail from "../media/images/email.svg";
import "../styles/contactText.css";

export default function ContactText()
{
	const nameRef = useRef('');
	const phoneRef = useRef('');
	const mailRef = useRef('');

	function sendMail()
	{
		const unirest = require('unirest');
		const request = unirest("POST", "https://mail-biotech.herokuapp.com/send-mail");
		let body = {
			"name": nameRef.current.value,
			"email": mailRef.current.value,
			"phone": phoneRef.current.value
		};
		request.headers({
			'Content-Type': 'application/json',
		});
		request.send(body);
		request.then(handleSendMailResponse);
	}

	function handleSendMailResponse(response)
	{
		if (response.code === 200)
		{
			alert("Mensaje enviado");
			nameRef.current.value = "";
			mailRef.current.value = "";
			phoneRef.current.value = "";
		}
		else
		{
			alert("Hubo un error al enviar el mensaje");
		}
	}

	return (<Fragment>
		<div className={'center-text'}>Queremos saber tus inquietudes</div>
		<div className={'information-container'}>
			<div className={'information'}>
				<div className={'phone'}>
					<div className={'icon'}>
						<Image className={'contact-logo'} src={Call}/>
					</div>
					<div className={'info-data'}>09 9999 0923</div>
				</div>
				<div className={'mail'}>
					<div className={'icon'}>
						<Image className={'contact-logo'} src={Mail}/>
					</div>
					<div className={'info-data'}>solutionsbiotechec@gmail.com</div>
				</div>
			</div>
			<div className={'send-email'}>
				<Form className={'contact-form'}>
					<Form.Group>
						<Form.Label>Nombre</Form.Label>
						<Form.Control ref={nameRef} type="text" placeholder="Escribe tu nombre"/>
					</Form.Group>
					<Form.Group>
						<Form.Label>Teléfono</Form.Label>
						<Form.Control ref={phoneRef} type="text" placeholder="Escribe tu teléfono"/>
					</Form.Group>
					<Form.Group>
						<Form.Label>Email</Form.Label>
						<Form.Control ref={mailRef} type="email" placeholder={"Escribe tu correo"}/>
					</Form.Group>
					<Form.Group>
						<Button onClick={sendMail}>Enviar</Button>
					</Form.Group>
				</Form>

			</div>
		</div>
	</Fragment>);
}