import React from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import Logo from "../media/images/Logo blanco.png"
import 'bootstrap/dist/css/bootstrap.min.css';
import '../styles/navigation bar.css';
import { Image } from "react-bootstrap";
import { NavLink } from "react-router-dom";


export default function NavigationBar()
{
	return (<Navbar bg="dark" variant="dark">
				<Navbar.Brand href="/home">
					<Image src={Logo}  width={75} height={75} rounded className={"logo"}/>
					Bio-tech Solutions
				</Navbar.Brand>
				<Nav className="mr-auto">
					<NavLink className={"nav-link"} to="/home">Inicio</NavLink>
					<NavLink className={"nav-link"} to="/products">Productos</NavLink>
					<NavLink className={"nav-link"} to="/news">Noticias</NavLink>
					<NavLink className={"nav-link"} to="/contact">Contacto</NavLink>
				</Nav>
		</Navbar>
	);
}